import React from 'react';
import type { ICommonComponentProps } from '../baseItem/interface';
import { Popover } from '../../../display/popover';
import { GuidePanel } from '../../../office/guide-panel';
import { TooltipContent, tooltipOverlayRestStyle } from '../../../display/tooltip/Tooltip';

const placementOffset: [number, number] = [4, 0];
export const ToolbarHoverTip: React.FC<{
  disabled?: boolean;
  tooltip: ICommonComponentProps['tooltip'] | false;
  overlayVisible: boolean;
  children: React.ReactElement;
}> = React.memo((props) => {
  const { children, disabled, tooltip, overlayVisible } = props;

  let contentNode: React.ReactNode | undefined;
  let overlayStyle: React.CSSProperties | undefined;
  let placement: 'bottomLeft' | 'bottom' = 'bottom';

  if (!overlayVisible && tooltip) {
    if (typeof tooltip === 'object' && 'title' in tooltip) {
      const { title, description, shortCut } = tooltip;
      if (disabled) {
        if (!('image' in tooltip)) {
          contentNode = <TooltipContent title={title} description={description} />;
          overlayStyle = tooltipOverlayRestStyle;
        }
      } else if ('image' in tooltip) {
        contentNode = <GuidePanel title={`${title} ${shortCut ? ` (${shortCut})` : ''}`} description={description} image={tooltip.image} />;
        placement = 'bottomLeft';
      } else {
        contentNode = <TooltipContent title={`${title} ${shortCut ? ` (${shortCut})` : ''}`} description={description} />;
        overlayStyle = tooltipOverlayRestStyle;
      }

    } else {
      contentNode = <TooltipContent title={tooltip} />;
      overlayStyle = tooltipOverlayRestStyle;
    }
  }

  // @ts-ignore
  return (
    <Popover
      trigger='hover'
      placement={placement}
      content={contentNode}
      style={overlayStyle}
      placementOffset={placementOffset}
      // visible={!!contentNode}
    >
      {children}
    </Popover>
  );  
});